.section-wrapper{
	margin: 25px 0;
}

// contains either the image or text block
.section-container{
	h2{ 
		color: $color__link; 
	}


}

// div that wraps the image content
.image-wrapper{
	position: relative;
	margin: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;

	.image-container{
		width: 100%;
		height: 0;
		padding-top: 100%;
		z-index: 1;
	}

	.caption-wrapper{
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 2;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 1em;

		p{ 
			color: white; 
			text-align: center;
		}
	}

}


// filter used on the image content when there's a caption on the image
.filtered{
	position: relative;
	z-index: 1;
	&:before{
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0,0,0,0.5);
		z-index: -1;
	}

}


.text-wrapper{
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100%;
	text-align: center;
	padding: 1em;
	h2{ font-weight: 400; }
	a{
		display: block;
		padding: 8px 10px 5px 8px;
		border: 2px solid $color__link;
		margin-bottom: 1em;
		transition: 0.5s ease-in-out;

		&:hover{
			background: $color__link;
			color: white;
		}
	}
}


@media screen and (min-width: 768px){
	.section-wrapper{
		display: flex;
		justify-content: space-around;
	}

	.column-1{
		width: 100%;
		a{ 
			display: inline-block; 
		}
	}

	.column-2{
		width: 50%;
		.image-wrapper{
			margin: 0;
			padding: 0.5em;

		}
		.text-wrapper{
			text-align: left;
			h2{ 
				margin: 0;
			}
			
			.button-wrapper{
				a{ 
					margin-bottom: 0;
					display: inline-block;
				}
			}
		}
	}

	.column-3{

		width: 32.5%;
		text-align: center;
		overflow: hidden;

		.image-wrapper{ margin: 0; }

		h2{ 
			margin: 0;
		}

		.image-container{
			transition: 0.5s ease-in-out;
		}
		&:hover{
			.image-container{ transform: scale(1.4); }
			.caption-wrapper{
				h2{ color: $color__link-lighter; }
			}
		}
	}
}

@media screen and (min-width: 940px){
	.section-wrapper{
		
		.text-wrapper{
			.button-wrapper{
				a{  font-size: 16px; }
			}			
		}
	}
}

@media screen and (min-width: 1024px){
	.column-1,
	.column-2,
	.column-3{
		.text-wrapper{
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}

	.column-1{
		padding-top: 50px;
		padding-bottom: 50px;
	}

}

@media screen and (min-width: 1200px){
	.section-wrapper{
		max-width: 940px;
		margin: 50px auto;

		.text-wrapper{
			.button-wrapper{
				a{ font-size: 18px; }
			}
		}
	}
}

@media screen and (min-width: 1361px){
	.section-wrapper{
		max-width: 1200px;
	}
}